.pageWrapper {
  width: 100%;
  height: 93vh;
  pointer-events: none;
  position: relative;
}

.pageContentWrapper {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  padding: 25px 28px;
  width: 100%;
  height: 82vh;
  align-content: flex-start;
}

.underConstruction {
  position: absolute;
  filter: blur(3px);
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
