.rearrangeButtonWrapper {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  position: relative;
  width: 130px;
  transition: 200ms width cubic-bezier(0.075, 0.82, 0.165, 1);
  overflow: hidden;
}

.rearrangeButtonWrapperInnter {
  min-width: 255px;
}

.cancelIcon {
  height: 11px;
  width: 15px;
  margin-bottom: 1px;
}

.saveIcon {
  width: 18px;
  height: 11px;
  margin-bottom: 1px;
}

.cancelDNDState {
  color: #768093;
  cursor: pointer;
  position: relative;

  &:hover {
    color: #334057;
    text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  }

  &:hover .cancelIcon {
    color: red;
  }
}

.saveDNDState {
  color: #768093;
  margin: 0 5px 0 10px;
  cursor: pointer;
  position: relative;

  &:hover {
    color: #334057;
    text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  }

  &:hover .saveIcon {
    color: #00b38c;
  }
}

.rearrangeButton {
  cursor: pointer;

  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    left: 51%;
    right: 51%;
    bottom: 0;
    background: #2098d1;
    height: 3px;
    transition-property: left, right;
    transition-duration: 200ms;
    transition-timing-function: ease-in-out;
  }

  &:hover:before,
  &.active:before {
    left: 0;
    right: 0;
  }

  &:hover,
  &.active {
    color: #334057;
    text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  }

  .faArrowsAltV {
    position: relative;
    margin-right: 7px;
    margin-left: 5px;
    transform: rotate(45deg);
  }
}

.saveCancelAppsOrder {
  transition: 400ms opacity cubic-bezier(0.075, 0.82, 0.165, 1);
  opacity: 0;
}

.enterDone {
  width: 255px;
}

.appear {
  opacity: 0.1;
}

.saveCancelAppsOrderEnterDone {
  opacity: 1;
}
