$borderColor: #d5dbe7;
$borderColorHovered: rgba(118, 128, 147, 0.7);

.cardWrapper {
  border: 1px solid $borderColor;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  height: 100%;
  transition: all 200ms ease-in-out;

  &:hover,
  &.hovered {
    box-shadow: 0 5px 30px rgba(118, 128, 146, 0.3);
    border: 1px solid $borderColorHovered;
  }

  &.disabled {
    pointer-events: none;
    .expandTicker {
      display: none;
    }
  }

  &.disabled:after {
    content: "";
    position: absolute;
    background: rgba(255, 255, 255, 0.7);
    width: 100%;
    height: 100%;
    top: 0;
  }
}

.nodeCardSummaryWrapper {
  background: #f9fafb;
  align-items: center;
  display: flex;
  height: 100%;
}

.section {
  padding: 0 25px;
  height: 50%;
  display: flex;
  align-items: center;
  border-right: 1px solid #e8ebef;
}

.IconSectionBackground {
  background-color: #ecedf4;
  height: 100%;
}

.deviceContentWrapper {
  display: flex;
  flex-flow: column;
  justify-content: space-evenly;
  align-items: flex-start;
}

.deviceType {
  width: 16%;
}

.deviceName {
  width: 28%;

  & > div {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 100%;
  }
}

.deviceSettingsMenu {
  width: 92px;
  padding: 0 25px;
  align-items: flex-end;
}

.deviceSettingsMenuIcon {
  padding: 2px 5px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
  border: 1px solid transparent;
  vertical-align: middle;
  border-radius: 4px;
}

.deviceSettingsMenuIcon:hover {
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(118, 128, 146, 0.32);
  box-shadow: 0px 3px 16px -3px rgba(118, 128, 146, 0.52);
}

.deviceStatus {
  width: 16%;
}

.nodeSettingPageWrapper {
  height: 66.5%;
  display: flex;
}

.placeHolderWidth {
  width: 78px;
}
.nodeSettingPage {
  width: 100%;
  margin: 0 25px;
  border-top: 1px solid #e8ebef;
}

.modalWrapper {
  border: 1px solid rgba(118, 128, 147, 0.7);
  min-width: 98.9%;
  height: 100%;
  border-radius: 8px;
  padding: 0;
  display: flex;
  margin: 0;
  flex-direction: column;
}
