$borderColor: #e8ebef;
$borderColorHovered: rgba(118, 128, 147, 0.7);
$hoverTransition: border 200ms ease-in-out, box-shadow 200ms ease-in-out, background-color 200ms ease-in-out;

.pageTitleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.mkAppModalWrapper {
  height: 100%;
  max-width: 100%;
  width: 100%;
  border-radius: 6px;
}

.appName {
  text-shadow: 2px 2px 3px rgba(32, 44, 61, 0.2);
  color: #2c3649;
  margin-bottom: 12px;
  margin-left: 2px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.pageWrapper {
  width: 100%;
  min-height: 790px;
}

.applicationTile {
  height: 100px;
  width: 180px;
  border: 1px solid #c3cbd5;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px 10px;
  border-radius: 10px;
  background-color: #f9fafb;
  transition: $hoverTransition;

  &.withDndStyles {
    transition: $hoverTransition;
    transition: none;
    border: 1px solid rgba(0, 151, 189, 0.76);
    background: #dcf2fe;
    box-shadow: 5px 5px 20px rgba(108, 118, 136, 0.4);
    opacity: 1;
    cursor: move;

    &:hover {
      .openAppInner {
        border: 1px solid rgba(0, 151, 189, 0.76);
        background: #9bdbfd;
        transform: translateY(13px);
      }
    }

    .openAppInner {
      border: 1px solid rgba(0, 151, 189, 0.76);
      background: #9bdbfd;
      transform: translateY(13px);
    }
  }

  &:hover {
    box-shadow: 5px 5px 20px rgba(108, 118, 136, 0.4);
    border: 1px solid rgba(118, 128, 147, 0.7);

    .openAppInner {
      transition: $hoverTransition;
      transform: translateY(0);
    }
  }

  .openApp {
    position: absolute;
    bottom: 0;
    width: 90%;
    height: 18px;
    left: 10px;
    overflow: hidden;
    cursor: pointer;
  }

  .openAppInner {
    height: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(13px);
    border-radius: 6px 6px 0 0;
    border: 1px solid rgba(118, 128, 147, 0.7);
    border-bottom: 0;
    background-color: #ecedf4;
    transition: transform 200ms ease-in-out;
  }

  .openAppText {
    height: 18px;
    text-transform: uppercase;
    height: 11px;
    color: rgb(118, 128, 147);
  }
}

.dndPlaceHolder {
  content: "";
  display: block;
  height: 100px;
  width: 180px;
  border-radius: 10px;
  border: 3px dashed #9bdbfd;
  position: absolute;
}

.optionsMenu {
  width: 78px;
  height: 33px;
  position: absolute;
  right: 2px;
  top: 7px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #768093;
  padding: 5px 6px 5px 6px;
  transition: $hoverTransition;

  &.appIsLoading {
    pointer-events: none;
  }

  &.withDndStyles {
    color: rgba(0, 151, 189, 0.76);

    .optionsItem:hover {
      color: rgba(0, 151, 189, 0.96);
    }
  }

  .optionsItem:hover {
    color: #2b3649;
    cursor: pointer;
  }

  .pinnedIcon {
    transform: rotate(45deg);
  }

  .pinned {
    transition: 200ms transform cubic-bezier(0.075, 0.82, 0.165, 1);
    transform: rotate(0deg);
    color: #00b38c;

    &:hover {
      color: #00cca0;
    }
  }
}

.pageContentWrapper {
  padding: 25px 57px;
  width: 100%;
  height: 82vh;
  display: grid;
  grid-template-columns: repeat(auto-fill, 180px);
  grid-gap: 20px;
  grid-template-rows: repeat(auto-fill, 100px);
}

.apploader {
  background: rgba(236, 237, 244, 0.8);
  position: absolute;
  top: 0px;
  left: 0px;
  border-radius: 10px;
  height: 98px;
  width: 178px;
}

.thumbnailWrapper {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  position: absolute;
  top: -10px;
  left: 10px;
  background-color: #fff;
  overflow: hidden;

  .textThumbnail {
    background-color: #ecedf4;
    font-size: 10px;
    line-height: 48px;
  }

  .textThumbnailLarge {
    transition: $hoverTransition;
    background-color: #ecedf4;
    color: #768093;
    font-size: 16px;
    line-height: 48px;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
  }

  &.withDndStyles {
    .textThumbnailLarge {
      color: #ffffff;
      background: #9bdbfd;
    }
  }
}

.applicationTileWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  grid-gap: 20px;
  transition: $hoverTransition;
}
