.dashboardWrapper {
  transition: all 700ms cubic-bezier(0.075, 0.82, 0.165, 1);
  margin: 0 25px 25px 0;
  height: 243px;
  width: 300px;
  border: 1px solid #e8ebef;
  border-radius: 6px;
  position: relative;
}

.dashboardWrapper:hover {
  box-shadow: rgba(23, 43, 99, 0.17) 0 1px 24px;
  transform: translateY(-5px);
}

.dashboardWrapper.smartHome {
  width: 320px;
  height: 200px;
}

.dashboardTitle {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 14px;
  color: #768093;
  background: #f9fafb;
  text-align: center;
  padding: 10px;
  position: relative;
  border-bottom: 1px solid #e8ebef;
}

.dashboardThumbnailWrapper {
  width: 60px;
  height: 60px;
  border: 1px solid #e8ebef;
  padding: 5px;
  border-radius: 6px;
  position: absolute;
  top: -10px;
  left: 14px;
  background-color: #fff;
  overflow: hidden;
}

.imageThumbnail {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 48px;
  width: 48px;
}

.imageSize {
  object-fit: scale-down;
  width: 100%;
}

.textThumbnail {
  background-color: #ecedf4;
  font-size: 10px;
  line-height: 48px;
}

.textThumbnailLarge {
  background-color: #ecedf4;
  font-size: 16px;
  line-height: 48px;
}

.dashboardContent {
  padding: 20px 15px;
  color: #768093;
  font-size: 14px;
  font-family: "Open Sans Normal", sans-serif;
}

.mkAppModalWrapper {
  width: 100%;
  height: 100%;
  max-width: 100%;
  border-radius: 10px;
}
