.contentHeader {
  background: rgb(5, 142, 217);
  display: flex;
  flex-direction: row;
  z-index: 1;
  width: 100%;
  box-shadow: 0 4px 10px rgba(5, 142, 217, 0.2);
  justify-content: space-between;
  height: 64px;
}

.sectionTitle {
  font-size: 1.5em;
  padding: 0 25px;
  font-family: "Open Sans Normal", sans-serif;
  font-weight: 300;
  color: #fff;
  text-shadow: 1px 1px 10px #5bc0fb;
  font-style: italic;
}

.headerUserThumbnail {
  background: #fff;
  height: 50px;
  width: 50px;
  border-radius: 3px;
  display: inline-block;
  margin-right: 15px;
  border: 3px solid rgba(5, 142, 217, 0.76);
}

.headerUserName {
  display: inline-block;
  font-family: "Open Sans Normal", sans-serif;
  position: relative;
  color: #fff;
  margin-right: 15px;
}

.headerOptions {
  width: 20px;
  height: 20px;
  background: rgba(249, 250, 251, 0.3);
  border-radius: 50%;
}

.headerTriangle {
  border-top: 5px solid #fff;
  height: 0;
  width: 0;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  margin: auto;
  top: 1px;
  position: relative;
  margin-left: 11px;
}

.logoutWrapper {
  padding: 25px;
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #ffffff;

  .logoutIcon {
    fill: #ffffff;
    height: 20px;
    width: 20px;
    margin-right: 15px;
  }
  &:hover {
    background: #24a2e5;
  }
}
