.transition {
  transition: all 250ms cubic-bezier(0.075, 0.665, 0.405, 0.995);
}

.animationBaseState {
  @extend .transition;
  height: 84px;
  width: 100%;
  position: relative;
  border-radius: 10px;
}

.overlay {
  background: rgba(255, 255, 255, 0.7);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.cardSettingsOpenned {
  @extend .transition;
  height: 79.2vh;
}

.cardSettingsClosed {
  @extend .transition;
  height: 100%;
}

.enter,
.enterDone,
.exit,
.exitActive {
  z-index: 1;
}

.exitDone {
  z-index: initial;
}

.masterNodeExpandedEnter,
.masterNodeExpandedDone {
  @extend .transition;
  height: 500px;
}
