$borderColor: #e8ebef;
$borderColorHovered: rgba(118, 128, 147, 0.7);
$slaveListTransition: all 350ms cubic-bezier(0.23, 1, 0.32, 1);

.nodeMasterCard {
  margin-bottom: 15px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  border-radius: 10px;

  &:hover {
    .expandTicker {
      border: 1px solid $borderColorHovered;
      border-bottom: 0;
      height: 17px;
      background-color: #ecedf4;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

.masterCardDetails {
  width: 100%;
  z-index: 1;
}

.nodeCardSummaryWrapper {
  position: relative;
  border-radius: 10px;
  width: 100%;
  height: 84px;
  border-bottom: 1px solid $borderColor;
}

.slavesWrapper {
  width: 103%;
  background-color: #ecedf4;
  height: 0;
  padding: 0 25px;
  border-radius: 10px;
  height: 0;
}

.slaveNodeCard {
  margin-bottom: 15px;
}

.nodeCardMargin {
  width: 100%;
  margin-bottom: 15px;
}

.expandTicker {
  overflow: hidden;
  border: 1px solid $borderColor;
  border-bottom: 0;
  margin: auto;
  height: 7px;
  width: 350px;
  text-align: center;
  border-radius: 10px 10px 0 0;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 1px;
  background-color: #ecedf4;
  transition: all 200ms ease-out;
  cursor: pointer;
}

.slavesListExpandEnterDone {
  height: 100%;
  overflow: initial;
  padding: 65px 1.6% 10px;
  margin-top: -50px;
  transition: $slaveListTransition;

  overflow: initial;
}

.slavesListExpandEnterActive {
  overflow: hidden;
  transition: $slaveListTransition;

  padding: 65px 1.6% 10px;
  margin-top: -50px;
  height: 100%;
}

.slaveListExpandExit {
  height: 0;
  padding: 0 20px;
  transition: $slaveListTransition;

  overflow: hidden;
}

.slaveListExpandExitDone {
  transition: $slaveListTransition;
  padding: 0 20px;
  opacity: 0;
  overflow: hidden;
  margin-top: -50px;
  margin: 0;
  height: 0;
}

.tickerPositionWrapper {
  width: 100%;
  position: relative;
}
