.pageWrapper {
  position: relative;
  height: 100%;
  min-width: 812px;
  min-height: 790px;
  flex-grow: 1;
}

.pageContentWrapper {
  display: flex;
  flex-flow: column;
  padding: 28px 50px;
  width: 100%;
  height: calc(100% - 90px);
  overflow-y: scroll;
  position: relative;
}

.cardWrapperSettingsLoaded {
  height: 77vh;
}

.pageHeaderWrapper {
  display: inline-flex;
  width: 98.5%;
  justify-content: space-between;
  align-items: center;
  color: #4a515e;
}

.displayMasterNode {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 14px;
  color: #768093;
}

.displayMasterNode input {
  margin-left: 5px;
}

.nodeSettingsSpinner {
  display: inline-block;
  padding: 28px 35px 20px 21px;
}
.nodeSettingsSpinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #768093;
  border-color: #768093 transparent #768093 transparent;
  animation: nodeSettingsSpinner 1.2s linear infinite;
}

.loading {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: 100px;
}

@keyframes nodeSettingsSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
