@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.spinner {
  display: inline-block;
  width: 48px;
  height: 48px;
}
.spinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #768093;
  border-color: #768093 transparent #768093 transparent;
  animation: spinner 1.2s linear infinite;
}

.loading {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
