.sideMenuWrapper {
  min-width: 230px;
  background: #202c3d;
  border-right: 1px solid #e8ebef;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 790px;
}

.sideMenuTitle {
  color: #768093;
  padding: 10px 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  font-size: 12px;
}

.sideMenuItem {
  text-shadow: 2px 2px 3px #202c3d;
  color: #929fb1;
  padding: 10px 25px;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  display: block;
  font-size: 12px;
  position: relative;
  cursor: pointer;
  text-transform: uppercase;
}

.sideMenuItemIcon {
  width: 0.825em;
  height: 25px;
  fill: currentColor;
  margin-right: 10px;
  vertical-align: middle;
  display: inline-block;

  .customHight {
    height: 25px;
  }
}

.sideMenuIPinnedApps {
  border-top: 1px solid #e8ebef;
  border-bottom: 1px solid #e8ebef;
  background: #ecedf4;
  padding: 10px 0;
  margin: 10px 0;
}

.sideMenuItem:hover {
  color: #aab0bb;
  background: #334057;
}

.selected.sideMenuItem .sideMenuItemIcon {
  fill: #058ed9;
}

.selected.sideMenuItem {
  color: #e6e9ef;
  background-color: #252e40;
  border-top: 1px solid #202737;
  border-bottom: 1px solid #202737;
  text-shadow: 2px 2px 3px #202c3d;
}

.selected.sideMenuItem:after {
  content: "";
  border-top: 14px solid #ffffff;
  border-left: 14px solid #ffffff;
  transform: rotate(45deg);
  position: absolute;
  right: -8px;
  height: 0;
  width: 0;
  top: 16px;
}

.selected.sideMenuItem:before {
  content: "";
  border-top: 14px solid #e8ebef;
  border-left: 14px solid #e8ebef;
  transform: rotate(45deg);
  position: absolute;
  right: -7px;
  height: 0;
  width: 0;
  top: 16px;
}

.generalSectionWrapper {
  background: #2b3649;
  padding: 20px 0;
  border-top: 1px solid #313d4e;
  margin-top: 3px;
  border-bottom: 1px solid #283445;
}

.pinnedSectionWrapper {
  border-top: 1px solid #313d4e;
  margin-top: 3px;
  background: #2b3649;
  padding: 20px 0;
  flex-grow: 1;
}

.pinnedApp {
  &.sideMenuItem {
    display: flex;
    justify-content: space-between;
  }

  &:hover .removePinnedAppIcon {
    opacity: 1;
    pointer-events: all;
  }

  .pinnedAppText {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    width: 84%;
    align-items: center;
  }

  .pinnedAppTextSmallScreen {
    display: none;
  }

  .removePinnedAppIcon {
    transition: 200ms opacity, min-width cubic-bezier(0.075, 0.82, 0.165, 1);
    opacity: 0;
    pointer-events: none;
    height: 15px;
    min-width: 15px;
    color: #aab0bb;
    position: relative;
    left: -1px;

    &:hover {
      color: #b8bdc6;
    }
  }
}

.pinnedEmptyState {
  display: flex;
  padding: 10px 25px;
  height: 100px;
  width: 229px;
  margin: auto;
  background-color: #252e40;
  border-top: 1px solid #202737;
  border-bottom: 1px solid #202737;
  text-shadow: 2px 2px 3px #202c3d;
  color: #929fb1;
  font-family: "Open Sans Bold", sans-serif;
  font-weight: 300;
  display: block;
  font-size: 12px;

  .explanation {
    padding-top: 5px;
    font-size: 10px;
  }
}

.loggedInUserSectionWrapper {
  background: #2b3649;
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  padding: 45px 0 30px;
  border-bottom: 1px solid #283445;

  .userName {
    color: #929fb1;
    text-shadow: 2px 2px 3px #202c3d;

    &:hover {
      color: #e6e9ef;
      cursor: pointer;
    }
  }

  .userNameCollapsed {
    display: none;
  }

  .userThumbnail {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-image: url(../../assets/img/captain-sparrow.jpg);
    background-size: 150%;
    background-position-x: -14px;
    background-position-y: -4px;
    margin-bottom: 15px;
  }
}

@media (max-width: 1042px) {
  .pinnedEmptyState {
    display: none;
  }

  .pinnedApp {
    .pinnedAppText {
      display: none;
    }

    .pinnedAppTextSmallScreen {
      display: block;
      font-size: 14px;
    }
  }

  .sideMenuWrapper {
    min-width: 75px;
  }

  .sideMenuTitle {
    padding: 10px 10px;
    text-align: center;
  }

  .sideMenuItem {
    padding: 10px 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .sideMenuItemIcon {
    margin-right: initial;
  }

  .menuItemText {
    display: none;
  }

  .loggedInUserSectionWrapper {
    .userName {
      display: none;
    }

    .userNameCollapsed {
      color: #768093;
      text-shadow: 2px 2px 3px #202c3d;
      display: initial;
      width: 50px;
      &:hover {
        color: #e6e9ef;
        cursor: pointer;
      }
    }

    .userThumbnail {
      width: 50px;
      height: 50px;
    }
  }
}
