.nodeSettingsWrapper {
  height: 100%;
  width: 100%;
  background: #f9fafb;
}

.nodeSettingsHeader {
  color: #1d1d1f;
  font-size: 18px;
  font-family: "Open Sans Bold", sans-serif;
  width: 100%;
  padding: 20px 20px 16px;
  line-height: 18px;
  background: #f3f4f8;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #e8ebef;
  box-shadow: 0 5px 15px rgba(232, 235, 239, 0.6);

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nodeSettingsHeader > div {
  display: inline-block;
}
.nodeSettingsHeaderDetails {
  font-size: 12px;
  font-family: "Open Sans Light", sans-serif;
  color: #1d1d1f;
}

.nodeSettingsHeaderDetails:before {
  content: " | ";
  font-size: 14px;
  font-family: "Open Sans Light", sans-serif;
  color: #1d1d1f;
  position: relative;
  bottom: 3px;
}

.nodeSettingsContent {
  height: calc(100% - 55px);
}

.loaderWrapper {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.closeButton {
  cursor: pointer;
}

.cardSettings {
  background: #f9fafb;
  border: 1px solid #e8ebef;
  border-radius: 10px;
  margin-bottom: 15px;
  box-shadow: 0 5px 30px rgba(118, 128, 146, 0.3);
  border: 1px solid rgba(118, 128, 147, 0.7);
  transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
  z-index: 1;
}
